import React, { useContext } from 'react';
import { AppContext } from '../App';
import CompHour from './CompHour';
import 'chart.js/auto';
import { Chart } from 'react-chartjs-2';
import { fnOutput } from '../store/function';
import ChartDataLabels from 'chartjs-plugin-datalabels';

const CompHourly = () => {
  let { stWeather } = useContext(AppContext)
  if (!stWeather) return false /* 비동기 */
  let hourlyArr = stWeather.hourly
  let labelsArr = new Array(24).fill('')

  let dataArr = hourlyArr.map((v, i) => {
    if (i <= 23) {
      let { temp } = fnOutput(v)
      return temp
    }
  })
  dataArr.splice(24, 24)

  const data = {
    labels: labelsArr,
    datasets: [{
      label: '',
      data: dataArr,
      borderWidth: 1,
      fill: false,
      borderColor: '#fff',
      tension: 0.2,
    }]
  }

  const options = {
    scales: {
      x: {
        display: false,
        grid: {
          display: false
        }
      },
      y: {
        display: true,
        beginAtZero: true,
        min: -50,
        max: 50,
        stepSize: 10,
        grid: {
          color: 'rgba(255,255,255,0.2)',
        },
        ticks: {//display : none 중
          display: false,

          stepSize: 10,
        },
      },
    },
    plugins: {
      legend: {//전체제목
        display: false,
        position: 'top',
        labels: {
          color: 'rgb(255, 99, 132)'
        }
      },//legend
      datalabels: {
        display: true,
        color: "white",
        align: 'top',
      },
    },
    responsive: true,
    maintainAspectRatio: false,
  }//options

  let gridArr = []
  for (let i = 5; i >= -5; i--) {
    gridArr.push(i * 10)
  }

  return (
    <section className='hourly-section'>
      <div className="inner scr-style">
        <h2>
          <i className="fa-regular fa-clock"></i>
          <span>Hourly Forecast</span>
        </h2>
        <div className="hourly-container">
          <ul>
            {
              hourlyArr.map((v, i) => {
                if (i <= 23) {
                  return <CompHour hourData={v} key={i} />
                }
              })//map

            }
          </ul>
          <div className="chart">
            <Chart type='line' data={data} options={options} plugins={[ChartDataLabels]} />
            <div className="grid">

              {
                gridArr.map((v, i) => {
                  return <span key={i}>{v}</span>
                })
              }

            </div>
          </div>{/* chart */}
        </div>{/*hourly-container  */}
      </div>{/* inner */}
    </section>
  );
};

export default CompHourly;