import React from 'react';
import { useContext } from 'react';
import { AppContext } from '../App';
import CompDay from './CompDay';

const CompDaily = () => {
  let { stWeather } = useContext(AppContext)
  if (!stWeather) return false
  let dailyArr = stWeather.daily


  return (
    <section className='daily-section'>
      <div className="inner scr-style">
        <h2>
          <i className="fa-solid fa-calendar-days"></i>
          <span>Daily Forecast</span>
        </h2>
        <div className="daily-container">
          <ul>
            {
              dailyArr.map((v, i) => {
                if (i <= 23) {
                  return <CompDay key={i} dayData={v} />
                }
              })//map

            }
          </ul>
        </div>
      </div>
    </section>
  );
};

export default CompDaily;