import React from 'react';
import { useEffect } from 'react';
import { useContext } from 'react';
import { AppContext } from '../App';
import { fnGetAddress, fnGetWeather } from '../store/function';

const CompMap = () => {
  let {
    stlatLng, stChangLatLng,
    stAddress, stChangAddress,
    stWeather, stChangWeatherData,
  } = useContext(AppContext)

  useEffect(() => {
    //useEffect안에서 return은 앱이 종료될 때를 의미
    if (stlatLng) {
      let map = new window.google.maps.Map(document.getElementById('map'), {
        center: { lat: stlatLng.lat, lng: stlatLng.lng },
        zoom: 8
      })//map
      let center = { lat: stlatLng.lat, lng: stlatLng.lng }

      map.addListener('click', async (e) => {
        let latlng = {
          lat: e.latLng.lat(),
          lng: e.latLng.lng()
        }
        let addressArr = await fnGetAddress(latlng.lat, latlng.lng)
        let address
        try {
          address = addressArr[2].formatted_address
        } catch {
          address = '주소를 가져올수 없습니다.'
        }
        let yn = window.confirm(`${address}\n해당위치의 날씨 정보로 변경하시겠습니까?`)
        if (yn) {
          stChangLatLng(latlng)
          stChangAddress(addressArr)
          let weather = await fnGetWeather(latlng.lat, latlng.lng)
          stChangWeatherData(weather)
          document.querySelector('.map-section').classList.remove('active')
          document.querySelector('.app-container').scrollTo({ top: 0 })//첫페이지로 이동
        }//if
      })//click
    }//if
  })//useEffect

  useEffect(() => {
    document.querySelector('.close-map').addEventListener('click', () => {
      document.querySelector('.map-section').classList.remove('active')
    })
  }, [])


  return (
    <section className='map-section'>
      <div id='map'></div>
      <button className='close-map'>
        <i className="fa-solid fa-x"></i>
      </button>
    </section>
  );
};

export default CompMap;