import React from 'react';
import { useContext } from 'react';
import { useEffect } from 'react';
import { AppContext } from '../App';
import { fnGetAddress, fnGetcurrentLocation, fnGetWeather } from "../store/function";


const CompHeader = () => {
  let { fnInit, stChangLatLng, stChangAddress, stChangWeatherData, } = useContext(AppContext)
  useEffect(() => {
    document.querySelector('.map-btn').addEventListener('click', () => {
      document.querySelector('.map-section').classList.add('active')
    })
    document.querySelector('.refresh-btn').addEventListener('click', () => {
      let yn = window.confirm('현재위치로 날씨정보를 변경하시겠습니까?')
      if (yn) {
        fnInit()
      }//click

      /*    
      let { lat, lng } = await fnGetcurrentLocation() // 현재 위치의 위경도를 객체(위도,경도)로 변환
      let address = await fnGetAddress(lat, lng) // 위경도를 받아서 주소를반환
      let weather = await fnGetWeather(lat, lng) // 위경도를 받아서 날씨정보를 반환
      let bg = weather.current.weather[0].main
      document.getElementById('root').style.backgroundImage = `url(${process.env.PUBLIC_URL}/img/weather/${bg}.jpg)`
      stChangLatLng({ lat, lng })
      stChangAddress(address)
      stChangWeatherData(weather) 
            */
    })
  }, [])
  return (
    <header>
      <button className="refresh-btn">
        <i className="fa-solid fa-rotate-right"></i>
      </button>
      <h1>
        <span className='hidden'>날씨앱</span>
        <img src={`${process.env.PUBLIC_URL}/img/main/title.png`} alt="" />
      </h1>

      <button className='map-btn'>
        <i className="fa-solid fa-map-location-dot"></i>
      </button>
    </header>
  );
};

export default React.memo(CompHeader);